import React, { useState } from 'react';
import { useSelector } from 'react-redux';
import MenuButton from '@components/MenuButton';
import Sidebar from '@components/Sidebar';
import type { TitleState } from '@store/modules/title';
import type { FiltersState } from '@store/modules/filters';

import { HeaderLayout, HeaderSideBox, HeaderParagraph } from './Styles';

interface HeaderProps {
  repoName: string;
  location: Location;
}

function Header({ repoName, location }: HeaderProps) {
  const title = useSelector<{ title: TitleState; filters: FiltersState }>(
    (state) => state.title.title
  );

  const [isMenuOpen, setMenuOpen] = useState(false);

  return (
    <HeaderLayout>
      <Sidebar isOpen={isMenuOpen} setIsOpen={setMenuOpen} />

      <HeaderSideBox>
        <MenuButton isOpen={isMenuOpen} setIsOpen={setMenuOpen} />
      </HeaderSideBox>

      <HeaderParagraph>
        {location.pathname === '/' || location.pathname === '/about'
          ? repoName
          : title}
      </HeaderParagraph>

      <HeaderSideBox />
    </HeaderLayout>
  );
}

export default Header;
